/* General */
.homepage-container {
    font-family: Helvetica, sans-serif;
    position: relative; /* Asegura que todo el contenido esté dentro del contenedor principal */
    height: 100vh; /* Ajusta la altura de la página */
    display: flex;
    flex-direction: column;
}

/* Cabecera de la imagen */
.header-image {
    position: relative;
    width: 100%;
    height: 50vh; /* Ajusta la altura de la imagen */
    background-image: url('../../assets/images/header3.jpg'); /* Ruta de la imagen */
    background-size: cover;
    background-position: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center; /* Centra el contenido verticalmente */
    text-align: center;
    color: white;
}

/* Capa oscura sobre la imagen */
.header-image::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5); /* Oscurecimiento */
    z-index: 1; /* Asegura que el oscurecimiento quede encima de la imagen */
}

/* Título y eslogan */
.header-title {
    font-size: 4rem; /* Tamaño de la fuente para el título */
    margin: 0;
    font-weight: bold;
    color: #ffffff; /* Blanco */
    z-index: 2; /* Asegura que el título esté por encima de la capa oscura */
}

.header-slogan {
    font-size: 2rem; /* Tamaño de la fuente para el eslogan */
    margin-top: 10px;
    color: #ffffff; /* Blanco */
    text-align: center;
    max-width: 90%; /* Limita el ancho del eslogan */
    word-wrap: break-word;
    z-index: 2; /* Asegura que el eslogan esté por encima de la capa oscura */
}

/* Contenedor de los botones (debajo del texto) */
.button-container-home {
    position: absolute; /* Posiciona los botones dentro de la imagen */
    bottom: 30px; /* Distancia desde el fondo de la imagen */
    left: 50%;
    transform: translateX(-50%); /* Centra los botones horizontalmente */
    display: flex;
    justify-content: center; /* Centra los botones horizontalmente */
    gap: 20px; /* Espacio entre los botones */
    z-index: 2; /* Asegura que los botones estén por encima de la capa oscura */
    width: 100%;
    text-align: center;
}

/* Estilo de los botones */
.nav-button-home {
    padding: 15px 30px;
    font-size: 1rem;
    background-color: #007bff; /* Color de los botones */
    color: white;
    border: none;
    border-radius: 50px; /* Esquinas redondeadas */
    cursor: pointer;
    transition: background-color 0.3s ease;
    width: auto;
    max-width: 250px; /* Limita el tamaño máximo de los botones */
}

.nav-button-home:hover {
    background-color: #0056b3; /* Color más oscuro al pasar el mouse */
}